.main-offer__slogan__wrapper {
  width: 100%;
  height: 390px;
  background-image: url("/img/main-background-mobile@1x.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top, bottom;
  margin-top: -40px;
  text-align: center;

  @media (min-resolution: 2dppx) {
    background-image: url("/img/main-background-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("/img/main-background-tablet@1x.jpg");
    height: 518px;
    margin: 0;

    @media (min-resolution: 2dppx) {
      background-image: url("/img/main-background-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("/img/main-background-desktop@1x.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("/img/main-background-desktop@2x.jpg");
    }
  }
}

.main-offer__slogan {
  width: 282px;
  height: 214px;
  margin: 90px 0 117px 0;

  @media (min-width: $tablet-width) {
    width: 460px;
    height: 350px;
  }
}

.main-offer__slogan picture {
  text-align: center;
}

.main-offer__title {
  @include title;
  width: 255px;
  text-align: center;
  margin: 32px auto 40px auto;

  @media (min-width: $tablet-width) {
    width: 450px;
  }
}

.main-offer__description {
  @include description;
  width: 270px;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 29px;

  @media (min-width: $tablet-width) {
    width: 540px;
  }
}
